<template>
    <transition name="slide">
        <div class="app" ref="app" :style="{minHeight: height + 'px'}">
            <div>
                <router-view class="content"></router-view>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
  data () {
    return {
      height: ''
    }
  },
  mounted () {
    var screabHeight = document.documentElement.clientHeight
    this.height = screabHeight
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

.app
    background-color $color-theme-gray_4
</style>





